    .title::before {
        // position: relative;
        display: inline-block;
        content: "";
        width  : 650px;
        height : 650px;
        background : transparent;
        border: #FFFFFF 2px solid;
        opacity: 0.3;
        position: absolute;
        transform: rotate(45deg);
        margin-top: 430px;
        // margin: 90px auto;
        // border-left: transparent;
        // border-right: transparent;
        // right: 535px;
        left: -440px;
        animation: animate1 4s linear infinite;
        box-shadow: rgba(7, 7, 105, 0.25) 0px 1100px 150px -12px inset, rgba(41, 9, 114, 0.3) 0px 528px 116px -18px inset;
    }

    .title::after {
        display: inline-block;
        content: "";
        width  : 350px;
        height : 350px;
        background : transparent;
        border: #FFFFFF 2px solid;
        opacity: 0.3;
        position: absolute;
        transform: rotate(45deg);
        margin-top: 580px;
        // margin: 90px auto;
        // border-left: transparent;
        // border-right: transparent;
        // right: 535px;
        left: -250px;
        animation: animate1 4s linear infinite;
        box-shadow: rgba(7, 7, 105, 0.25) 0px 1100px 150px -12px inset, rgba(41, 9, 114, 0.3) 0px 528px 116px -18px inset;
    }

    
    @keyframes animate1 {
        0% {
            border: blue 2px solid;
            // border-left: transparent;
            // border-right: transparent;
        }

        50% {
            border: purple 2px solid;
            // border-left: transparent;
            // border-right: transparent;
        }

        75% {
            border: white 2px solid;
            // border-left: transparent;
            // border-right: transparent;
        }

        100% {
            border: blue 2px solid;
            // border-left: transparent;
            // border-right: transparent;
        }
        
    }
    .title {
        padding-top: 85px;
        text-align: center;
        margin-bottom: 10vh;

        h3 {
            font-family: "Founders Grotesk Condensed Test Medium";
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            line-height: 29px;
            text-align: center;
            color: #D9D9D9;
            margin: 0;
        }
    }

    .left {
        margin-top: 1vh;
        width: 30%;
        text-align: right;
    }

    .right {
        margin-bottom: 3vh;
        margin-left: 3vw;
        width: 42%;
    }

    .left1 {
        margin-top: -1vh;
        width: 30%;
        text-align: right;
    }

    .left2 {
        margin-top: 0vh;
        width: 30%;
        text-align: right;
    }

    .left3 {
        margin-top: 0vh;
        width: 30%;
        text-align: right;
    }

    .right2 {
        margin-bottom: 5vh;
        margin-left: 3vw;
        width: 42%;

        h2 {
            font-family: "Söhne Breit Test";
            font-size: 30px;
            line-height: 35px;
            margin: 0;
        }
    }

    h1 {
        font-family: "Söhne Breit Test";
        font-size: 50px;
        line-height: 50px;
        text-align: center;
        display: inline-block;
      }

    h1::before {
        position: relative;
        display: inline-block;
        content: "";
        top: -13px;
        left: -28px;
        // background-image: url('./../img/button.png');
        background: linear-gradient(to top, rgb(93, 93, 223), rgb(39, 39, 250));
        background-size: cover;
        width: 12px;
        height: 12px;
        transform: rotate(-135deg);
    }

    h1:hover::before {
        border: 2px solid #FFFFFF;
        background: transparent;
        transition: all 0.5s ease-out;
        // animation: animate 1s linear forwards;
        -webkit-box-shadow: 0px 0px 0px 2px rgb(39, 39, 250); 
        box-shadow: 0px 0px 0px 2px rgb(39, 39, 250);
    }

    h1::after {
        position: relative;
        display: inline-block;
        content: "";
        top: -13px;
        right: -28px;
        // background-image: url('./../img/button.png');
        background: linear-gradient(to top, rgb(93, 93, 223), rgb(39, 39, 250));
        background-size: cover;
        width: 12px;
        height: 12px;
        transform: rotate(-135deg);
    }
    h1:hover::after {
        border: 2px solid #FFFFFF;
        background: transparent;
        transition: all 0.5s ease-out;
        // animation: animate 1s linear forwards;
        -webkit-box-shadow: 0px 0px 0px 2px rgb(39, 39, 250); 
        box-shadow: 0px 0px 0px 2px rgb(39, 39, 250);
    }

    h2 {
        font-family: "Söhne Breit Test";
        font-size: 30px;
        line-height: 28px;
        margin: 0; 
        color: #FFFFFF;   
    }
    h3 {
        // font-family: "Founders Grotesk Condensed Test Regular";
        font-family: "Founders Grotesk Condensed Test Light";
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        text-align: justify;
        color: #D9D9D9;
        margin-bottom: 5vh;
        margin-top: 2vh;
    }

    .sous_title {
        text-align: center;
    }
}

