#artiste {
    // margin:2 auto;
    position: relative;
    padding: 120px 0px 180px 0px;
    // max-width: 900px;
    width: 100%;
    background-color: #080101;
    // background-image: url('./../img/artiste_back.png');
    // background-position: bottom right;
    // background-repeat: no-repeat;
    // background-size: 100% 100%;
    border-top: #FFFFFF 1px solid;
    // text-align: center;


    .container::after {
        display: inline-block;
        content: "";
        width  : 550px;
        height : 30px;
        background : transparent;
        border: #FFFFFF 2px solid;
        opacity: 0.3;
        position: absolute;
        border-top: transparent;
        transform: rotate(160deg);
        box-shadow: rgba(7, 7, 105, 0.25) 0px 1100px 150px -12px inset, rgba(41, 9, 114, 0.3) 0px 528px 116px -18px inset;
        // margin-top: 580px;
        left: -600px;
        z-index: -1;
        animation: animate1 4s linear infinite;
    }

    .container::before  {
        display: inline-block;
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        border-radius: 6px;
        margin-top: 200px;
        left: 270px;
        // border-radius: 10px;
        background-image: url('./../img/artiste_back.png');
        // background-position: bot tom right;
        // background-repeat: no-repeat;
        background-size: 100% 100%;
        // z-index: -1;
    }

    .container {
        // display: flex;
        z-index: 2;
        height: 500px;
        width: 830px;
        margin: 0px 28vw;
        // border: #FFFFFF 1px solid;
        // margin-top: 150px;
        // -webkit-backdrop-filter: blur(10px);
        backdrop-filter: blur(37.5367px);
        background-color: transparent;
        // filter: blur(7.5367px);
        box-shadow: rgba(7, 7, 105, 0.25) 0px 200px 150px -12px inset, rgba(41, 9, 114, 0.3) 0px 458px 116px -18px inset;
        // background-c olor: #0e0c0c;
        // background-color: linear-gradient(85.96deg, rgba(0, 0, 0, 0.47) -20.51%, rgba(0, 0, 0, 0.47) 26.82%, rgba(0, 0, 0, 0.47) 65.65%), rgba(196, 196, 196, 0.05);
        // box-shadow: inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1), inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1);
        border-radius: 10px;

        hr {
            width: 100%;
            margin-top: 40px;
            // overflow: hidden;
            // transition:width 1s;
            height:0.1px;
            color: #D9D9D9;
            position: absolute;
            // left: 200px;
          }

        .left::before {
            display: inline-block;
            content: "";
            position: absolute;
            width: 407px;
            height: 446px;
            border-radius: 6px;
            margin-top: 90px;
            left: -80px;
            // border-radius: 10px;
            background-image: url('./../img/fifa1.jpeg');
            // background-size: contain contain;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;
        }
        
        // .left::after {
        //     display: inline-block;
        //     content: "";
        //     width  : 550px;
        //     height : 30px;
        //     background : transparent;
        //     border: #FFFFFF 2px solid;
        //     opacity: 0.3;
        //     position: absolute;
        //     border-top: transparent;
        //     transform: rotate(190deg);
        //     margin-top: 80px;
        //     left: 600px;
        //     z-index: -1;
        //     animation: animate1 4s linear infinite;
        // }

        .left {
            display: flex;
            flex-direction: column;
            padding: 0px 50px 0px 370px;
            // filter: blur(37.5367px);
            // position: absolute;
            // left: 45%;

            h2 {
                font-family: "Söhne Breit Test";
                font-size: 30px;
                line-height: 28px;
                margin: 0; 
                margin-top: 80px;
                color: #FFFFFF;   
            }

            #discord_jo {
                font-family: "Founders Grotesk Condensed Test Light";
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
                text-align: justify;
                color: #D9D9D9;
                margin-top: 10px;
                // margin-bottom: 16px;
            
                img {
                //   margin: -2% 6%;
                  text-align: right;
                }      
              }

              #twitter_jo {
                font-family: "Founders Grotesk Condensed Test Light";
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
                text-align: justify;
                color: #ffffff;
                margin-top: 15px;
            
                img {
                //   margin: -2% 6%;
                  text-align: right;
                }      
              }

            p {
                font-family: 'Founders Grotesk Condensed Test Light';
                font-style: normal;
                font-weight: 500;
                font-size: 22px;
                line-height: 26px;
                color: #959595;
                margin: 7px 0 40px 0;
                // margin-bottom: 40px;
            }
            h3 {
                font-family: "Founders Grotesk Condensed Test Light";
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
                text-align: justify;
                color: #ffffff;
            }
        }
    }
}

@media only screen and (max-width: 1250px) {
    #artiste {
        .container {
            margin: 0px auto;
        }
    }
}

@media only screen and (max-width: 1100px) {
    #artiste {
        // padding: 180px 0px 180px 40px;
        .container::after {
            display: none;
        }

        .container::before {
            display: none;
        }
        .container {
            height: 400px;
            max-width: 670px;
            margin: 0px auto;

            .left::before {
                width: 287px;
                height: 336px;
                margin-top: 90px;
                left: -45px;
            }

            .left {
                padding: 0px 50px 0px 285px;
            }
        }
    }
}

@media only screen and (max-width: 850px) {
    #artiste {
        padding: 130px 0px 130px 10px;
        .container {
            height: 370px;
            max-width: 500px;

            .left::before {
                width: 217px;
                height: 290px;
                margin-top: 100px;
                left: -40px;
            }

            .left {
                padding: 0px 20px 0px 205px;

                h2 {
                    font-size: 25px;
                    line-height: 27px;
                    margin: 0; 
                    margin-top: 80px;
                }
        
                  #twitter_jo {
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 22px;
                    text-align: justify;
                    color: #D9D9D9;
                    margin-bottom: 6px;
                
                    img {
                    //   margin: -2% 6%;
                      text-align: right;
                    }      
                  }
    
                p {
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 22px;
                    color: #959595;
                    margin: 7px 0 20px 0;
                }
                h3 {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 20px;
                    text-align: justify;
                    color: #D9D9D9;
                }
    
            }
        }
    }
}

@media only screen and (max-width: 600px) {
    #artiste {
        padding: 100px 0px 100px 10px;
        .container {
            height: auto;
            // max-width: 318px;
            max-width: 80vw;

            hr {
                display: none;
              }

            .left::before {
                width: 160px;
                height: 210px;
                margin-top: 30px;
                left: -30px;
            }

            .left {
                padding: 0px 10px 0px 145px;

                h2 {
                    font-size: 16px;
                    line-height: 16px;
                    margin: 0; 
                    margin-top: 20px;
                }
        
                  #twitter_jo {
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 16px;
                    text-align: justify;
                    color: #D9D9D9;
                    margin-bottom: 3px;
                
                    img {
                    //   margin: -2% 6%;
                      text-align: right;
                    }      
                  }
    
                p {
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 16px;
                    color: #959595;
                    margin: 7px 0 10px 0;
                }
                h3 {
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 16px;
                    text-align: justify;
                    color: #D9D9D9;
                }
    
            }
        }
    }
}